// --------------------------------

// Order

// --------------------------------

.order-1 {
    order: 1;
}
.order-2 {
    order: 2;
}
.order-3 {
    order: 3;
}

// --------------------------------

// Aspect Ratio

// --------------------------------

[class*="aspect-ratio"] {
    --aspect-ratio: 16/9;
    position: relative;
    height: 0;
    padding-bottom: calc(100% / (var(--aspect-ratio)));

    > * {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.aspect-ratio-16\:9 {
    --aspect-ratio: 16/9;
}
.aspect-ratio-4\:3 {
    --aspect-ratio: 4/3;
}
.aspect-ratio-1\:1 {
    --aspect-ratio: 1/1;
}

