// --------------------------------

// Typography

// --------------------------------

.truncate,
.text-truncate {
    // truncate text if it exceeds its parent
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.text-replace {
    // replace text with bg img
    overflow: hidden;
    color: transparent;
    text-indent: 100%;
    white-space: nowrap;
}

.break-word {
    overflow-wrap: break-word;
    min-width: 0;
}

