// --------------------------------

// Text Shadow

// --------------------------------

.text-shadow-xs {
    text-shadow: 0 1px 1px rgba(#000, 0.15);
}
.text-shadow-sm {
    text-shadow: 0 1px 2px rgba(#000, 0.25);
}
.text-shadow-md {
    text-shadow: 0 1px 2px rgba(#000, 0.1), 0 2px 4px rgba(#000, 0.2);
}
.text-shadow-lg {
    text-shadow: 0 1px 4px rgba(#000, 0.1), 0 2px 8px rgba(#000, 0.15), 0 4px 16px rgba(#000, 0.2);
}
.text-shadow-xl {
    text-shadow: 0 1px 4px rgba(#000, 0.1), 0 2px 8px rgba(#000, 0.15), 0 4px 16px rgba(#000, 0.2),
        0 6px 24px rgba(#000, 0.25);
}
.text-shadow-none {
    text-shadow: none;
}

// --------------------------------

// .text-component vertical spacing

// --------------------------------

.v-space-xxs {
    --text-vspace-multiplier: 0.25 !important;
}
.v-space-xs {
    --text-vspace-multiplier: 0.5 !important;
}
.v-space-sm {
    --text-vspace-multiplier: 0.75 !important;
}
.v-space-md {
    --text-vspace-multiplier: 1.25 !important;
}
.v-space-lg {
    --text-vspace-multiplier: 1.5 !important;
}
.v-space-xl {
    --text-vspace-multiplier: 1.75 !important;
}
.v-space-xxl {
    --text-vspace-multiplier: 2 !important;
}

