/*
|
| absolute-full
|-----------------
*/
.absolute-full,
.absolute-top-left,
.absolute-bottom-left,
.absolute-top-right,
.absolute-bottom-right,
.absolute-top-left,
.absolute-center,
.absolute-center-y {
    position: absolute;
}

.absolute-full {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.absolute-top-left {
    top: 0;
    left: 0;
}

.absolute-bottom-left {
    bottom: 0;
    left: 0;
}

.absolute-top-right {
    top: 0;
    right: 0;
}

.absolute-bottom-right {
    bottom: 0;
    right: 0;
}

.absolute-center {
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}

.absolute-center-y {
    top: 50%;
    transform: translateY(-50%);
}

/*
|
| Flex Alignment
|-----------------
*/
.ai-start{ align-items: flex-start; }
.ai-end{ align-items: flex-end; }
.ai-center{ align-items: center; }
.ai-baseline{ align-items: baseline; }
.ai-stretch{ align-items: stretch; }

/*
|
| Flex Justify
|---------------
*/
.jc-start{ justify-content: flex-start; }
.jc-end{ justify-content: flex-end; }
.jc-center{ justify-content: center; }
.jc-between{ justify-content: space-between; }
.jc-around{ justify-content: space-around; }

/*
|
| Flex full center
|-------------------
*/
.flex-center{
    display: flex;
    align-items: center;
    justify-content: center;
}


/*
|
| svg parent
|--------------
*/
.svg-parent{
    svg, img{
        width: 100%;
        height: auto;
    }
}


.p-r-z-1{
    position: relative;
    z-index: 1;
}

.ta-c{
    text-align: center;
