		*/ }
  a.link.primary {
    padding: 24px 40px; }
    a.link.primary span {
      position: relative;
      z-index: 10; }
    a.link.primary:after {
      content: "";
      position: absolute;
      height: 100%;
      width: 100%;
      bottom: 0;
      left: 0;
      z-index: 1;
      background: #FBAA0B;
      transform: scaleX(0);
      transform-origin: right;
      transition: transform 1.2s cubic-bezier(0.19, 1, 0.22, 1); }
    a.link.primary.is-blue {
      background: #081746;
      color: #fff; }
    a.link.primary.is-small {
      padding: 12px 24px;
      font-size: 12px; }
    a.link.primary:hover:after {
      transform: scaleX(1);
      transform-origin: left; }
  a.link.secondary {
    display: flex;
    align-items: center; }
    a.link.secondary span {
      position: relative;
      z-index: 10;
      margin-left: 80px; }
    a.link.secondary:before {
      content: "";
      position: absolute;
      height: 10px;
      width: 10px;
      left: 0;
      z-index: 1;
      background: #fff;
      border: 16px solid #FBAA0B;
      border-radius: 50%;
      transform: scale(1);
      transition: transform 1.2s cubic-bezier(0.19, 1, 0.22, 1); }
    a.link.secondary:after {
      content: "";
      position: absolute;
      height: 1px;
      width: 28px;
      left: 0;
      margin-left: 32px;
      z-index: 2;
      transform: scaleX(1);
      transform-origin: left;
      transition: transform 0.64s cubic-bezier(0.19, 1, 0.22, 1); }
    a.link.secondary.is-blue {
      color: #081746; }
      a.link.secondary.is-blue:after {
        background: #081746; }
    a.link.secondary.is-white {
      color: #fff; }
      a.link.secondary.is-white:after {
        background: #fff; }
    a.link.secondary.is-orange {
      color: #FBAA0B; }
      a.link.secondary.is-orange:after {
        background: #fff; }
    a.link.secondary:hover:before {
      transform: scale(1.4); }
    a.link.secondary:hover:after {
      transform: scaleX(0);
      transform-origin: right; }

